import React, { Component } from 'react'
import PropTypes from 'prop-types';


export default class ImageButton extends Component {
	constructor(props) {
		super(props)
		this.doSelect = this.doSelect.bind(this)
	}

	doSelect = (e) => {
		if (this.props.isSelected) this.props.onSelect(e)
	}//doSelect

	render() {
		const iconSource = this.props.isSelected ? this.props.selectedImageURL : this.props.imageURL
		return (
			<span style={{
				position: 'absolute',
				top: this.props.top + 'px',
				left: this.props.left + 'px'
			}}>
				<img src={iconSource}
					alt={this.props.id}
					onSelect={this.doSelect} />
			</span>
		)
	}//render
}

ImageButton.propTypes = {
	top: PropTypes.number,
	left: PropTypes.number,
	id: PropTypes.string,
	isSelected: PropTypes.bool,
	imageURL: PropTypes.string,
	selectedImageURL: PropTypes.string,
	onSelect: PropTypes.func.isRequired
};

ImageButton.defaultProps = {
	onSelect: () => { console.log("INFO ImageButton :: please pass a function for onSelect") }
};



